/**
 * Dashboard Data Management Hook
 * ÎåÄÏãúÎ≥¥Îìú Îç∞Ïù¥ÌÑ∞ Í¥ÄÎ¶¨ ÌõÖ - ÏàòÎèô ÏÉàÎ°úÍ≥†Ïπ® Î∞©Ïãù
 */

import { useState, useCallback } from 'react';
import { useQuery } from '@tanstack/react-query';
import { dashboardApi } from '../../../api/dashboard';

// Types
interface DashboardStats {
  // Basic WordPress stats
  posts?: number;
  pages?: number;
  comments?: {
    total: number;
    pending: number;
  };
  views?: number;
  
  // User stats
  users: number | {
    total: number;
    pending: number;
    today: number;
    activeRate: number;
    change: number;
    trend: 'up' | 'down';
  };
  
  // Ecommerce stats (optional)
  sales?: {
    today: number;
    changePercent: number;
    monthlyTotal: number;
    monthlyTarget: number;
    trend: 'up' | 'down';
  };
  products?: number | {
    active: number;
    lowStock: number;
    newThisWeek: number;
    bestsellers: Array<{
      id: string;
      name: string;
      sales: number;
    }>;
    change: number;
    trend: 'up' | 'down';
  };
  content?: {
    publishedPages: number;
    draftContent: number;
    totalMedia: number;
    todayViews: number;
    change: number;
    trend: 'up' | 'down';
  };
  partners?: {
    active: number;
    pending: number;
    totalCommission: number;
    topPartners: Array<{
      id: string;
      name: string;
      commission: number;
    }>;
    change: number;
    trend: 'up' | 'down';
  };
}

interface ChartData {
  sales: Array<{
    date: string;
    amount: number;
    orders: number;
  }>;
  orders: Array<{
    status: string;
    count: number;
    color: string;
  }>;
  users: Array<{
    date: string;
    newUsers: number;
    activeUsers: number;
  }>;
}

interface NotificationItem {
  id: string;
  type: 'urgent' | 'approval' | 'success' | 'info';
  title: string;
  message: string;
  time: string;
  read: boolean;
  actionUrl?: string;
}

interface ActivityItem {
  id: string;
  type: 'user' | 'order' | 'product' | 'content';
  message: string;
  time: string;
  user?: string;
  icon: string;
}

interface SystemHealth {
  api: {
    status: 'healthy' | 'warning' | 'error';
    responseTime: number;
    lastCheck: string;
  };
  database: {
    status: 'healthy' | 'warning' | 'error';
    connections: number;
    lastCheck: string;
  };
  storage: {
    status: 'healthy' | 'warning' | 'error';
    usage: number;
    total: number;
  };
  memory: {
    status: 'healthy' | 'warning' | 'error';
    usage: number;
    total: number;
  };
}

export const useDashboardData = () => {
  const [manualRefreshTrigger, setManualRefreshTrigger] = useState(0);

  // Stats data query
  const {
    data: stats,
    isLoading: statsLoading,
    error: statsError,
    refetch: refetchStats
  } = useQuery<DashboardStats>({
    queryKey: ['dashboard', 'stats', manualRefreshTrigger],
    queryFn: () => dashboardApi.getStats(),
    enabled: true, // ÏûêÎèô Î°úÎî© ÌôúÏÑ±Ìôî
    staleTime: 5 * 60 * 1000, // 5Î∂ÑÍ∞Ñ fresh
    gcTime: 10 * 60 * 1000, // 10Î∂ÑÍ∞Ñ Ï∫êÏãú Ïú†ÏßÄ
    retry: 2
  });

  // Chart data query
  const {
    data: chartData,
    isLoading: chartLoading,
    error: chartError,
    refetch: refetchCharts
  } = useQuery({
    queryKey: ['dashboard', 'charts', manualRefreshTrigger],
    queryFn: async (): Promise<ChartData> => {
      const data = await dashboardApi.getChartData();
      return {
        sales: data.sales as Array<{ date: string; amount: number; orders: number; }>,
        orders: data.orders,
        users: data.users
      };
    },
    enabled: true,
    staleTime: 5 * 60 * 1000,
    gcTime: 10 * 60 * 1000,
    retry: 2
  });

  // Notifications query
  const {
    data: notificationsData,
    isLoading: notificationsLoading,
    refetch: refetchNotifications
  } = useQuery<{
    items: NotificationItem[];
    total: number;
    urgent: number;
    approval: number;
  }>({
    queryKey: ['dashboard', 'notifications', manualRefreshTrigger],
    queryFn: () => dashboardApi.getNotifications(),
    enabled: true,
    staleTime: 2 * 60 * 1000, // ÏïåÎ¶ºÏùÄ 2Î∂ÑÍ∞ÑÎßå fresh
    retry: 2
  });

  // Activities query
  const {
    data: activities,
    isLoading: activitiesLoading,
    refetch: refetchActivities
  } = useQuery<ActivityItem[]>({
    queryKey: ['dashboard', 'activities', manualRefreshTrigger],
    queryFn: () => dashboardApi.getRecentActivities(),
    enabled: true,
    staleTime: 3 * 60 * 1000, // 3Î∂ÑÍ∞Ñ fresh
    retry: 2
  });

  // System health query
  const {
    data: systemHealth,
    isLoading: healthLoading,
    refetch: refetchHealth
  } = useQuery<SystemHealth>({
    queryKey: ['dashboard', 'health', manualRefreshTrigger],
    queryFn: () => dashboardApi.getSystemHealth(),
    enabled: true,
    staleTime: 30 * 1000, // 30Ï¥àÍ∞ÑÎßå fresh (ÏãúÏä§ÌÖú ÏÉÅÌÉúÎäî ÏûêÏ£º ÌôïÏù∏)
    retry: 1
  });

  // Ï†ÑÏ≤¥ Î°úÎî© ÏÉÅÌÉú
  const isLoading = statsLoading || chartLoading || notificationsLoading || 
                   activitiesLoading || healthLoading;

  // Ï†ÑÏ≤¥ ÏóêÎü¨ ÏÉÅÌÉú
  const error = statsError || chartError;

  // ÏàòÎèô ÏÉàÎ°úÍ≥†Ïπ® Ìï®Ïàò
  const refreshAllData = useCallback(async () => {
    const timestamp = Date.now();
    setManualRefreshTrigger(timestamp);

    // Î™®Îì† ÏøºÎ¶¨Î•º Î≥ëÎ†¨Î°ú Ïã§Ìñâ
    const refreshPromises = [
      refetchStats(),
      refetchCharts(),
      refetchNotifications(),
      refetchActivities(),
      refetchHealth()
    ];

    await Promise.allSettled(refreshPromises);
  }, [refetchStats, refetchCharts, refetchNotifications, refetchActivities, refetchHealth]);

  // Ï¥àÍ∏∞ Îç∞Ïù¥ÌÑ∞ Î°úÎìú Ï†úÍ±∞ - enabled: trueÎ°ú ÏûêÎèô Î°úÎìúÎê®
  // useEffect(() => {
  //   refreshAllData();
  // }, []);

  // Í∞úÎ≥Ñ ÏÑπÏÖò ÏÉàÎ°úÍ≥†Ïπ® Ìï®ÏàòÎì§
  const refreshStats = useCallback(() => refetchStats(), [refetchStats]);
  const refreshCharts = useCallback(() => refetchCharts(), [refetchCharts]);
  const refreshNotifications = useCallback(() => refetchNotifications(), [refetchNotifications]);

  return {
    // Data
    stats,
    chartData,
    notifications: notificationsData,
    activities,
    systemHealth,

    // Loading states
    isLoading,
    error,

    // Refresh functions
    refreshAllData,
    refreshStats,
    refreshCharts,
    refreshNotifications,

    // Individual loading states (for granular control)
    loadingStates: {
      stats: statsLoading,
      charts: chartLoading,
      notifications: notificationsLoading,
      activities: activitiesLoading,
      health: healthLoading
    }
  };
};