// Dashboard Stats type
interface DashboardStats {
  users: {
    total: number;
    pending: number;
    today: number;
    activeRate: number;
    change: number;
    trend: 'up' | 'down';
  };
  sales: {
    today: number;
    changePercent: number;
    monthlyTotal: number;
    monthlyTarget: number;
    trend: 'up' | 'down';
  };
  products: {
    active: number;
    lowStock: number;
    newThisWeek: number;
    bestsellers: Array<{
      id: string;
      name: string;
      sales: number;
    }>;
    change: number;
    trend: 'up' | 'down';
  };
  content: {
    publishedPages: number;
    draftContent: number;
    totalMedia: number;
    todayViews: number;
    change: number;
    trend: 'up' | 'down';
  };
  partners: {
    active: number;
    pending: number;
    totalCommission: number;
    topPartners: Array<{
      id: string;
      name: string;
      commission: number;
    }>;
    change: number;
    trend: 'up' | 'down';
  };
  apps: {
    forum: {
      posts: number;
      activeUsers: number;
      pendingModeration: number;
    };
    signage: {
      displays: number;
      activeDisplays: number;
      content: number;
    };
    crowdfunding: {
      campaigns: number;
      totalRaised: number;
      backers: number;
    };
  };
}

